background-color: #C68B59;\r\n    display: grid;\r\n    grid-template-columns: auto 1fr auto;\r\n    gap: 10px;\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: 99;\r\n    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n    height: 60px;\r\n  }\r\n   \r\n  .app-bar .app-bar__menu {\r\n    display: flex;\r\n    align-items: center;\r\n  }\r\n   \r\n  .app-bar .app-bar__menu button {\r\n    background-color: transparent;\r\n    border: none;\r\n    font-size: 18px;\r\n    padding: 8px;\r\n    cursor: pointer;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n  }\r\n   \r\n  .app-bar .app-bar__brand {\r\n    display: flex;\r\n    align-items: center;\r\n  }\r\n   \r\n  .app-bar .app-bar__brand h1 {\r\n    color: white;\r\n    text-transform: uppercase;\r\n    font-size: 22px;\r\n    user-select: none;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation {\r\n    position: absolute;\r\n    top: 50px;\r\n    left: -180px;\r\n    width: 150px;\r\n    transition: all 0.3s;\r\n    padding: 8px;\r\n    background-color: #C68B59;\r\n    overflow: hidden;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation.open {\r\n    left: 0;\r\n  }\r\n   \r\n  .app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    text-decoration: none;\r\n    color: white;\r\n    padding: 8px;\r\n    margin-bottom: 5px;\r\n    width: 100%;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n  }\r\n   \r\n  /* hero jumbotron */\r\n  .hero {\r\n    display: grid;\r\n    align-items: center;\r\n    min-height: 380px;\r\n    width: 100%;\r\n    text-align: center;\r\n    background-image: url(\"../public/images/heros/ibumuda.jpeg\");\r\n    background-position: center;\r\n    background-size: cover;\r\n    margin-top: auto;\r\n  }\r\n  \r\n  .hero-inner {\r\n    margin: 0 auto;\r\n    max-width: 800px;\r\n  }\r\n  \r\n  .hero-title {\r\n    font-weight: 400;\r\n    color:white;\r\n    font-size: 60px;\r\n    text-shadow: 2px 2px #C68B59;\r\n    font-weight: bolder;\r\n  }\r\n  \r\n  .he